@use "global" as *;
.p-rooms{
// margin-top: rem(180);
// margin-bottom: rem(100);    
background: $light-blue;
padding-bottom: rem(120);
position: relative;
padding-left: rem(40);
padding-right: rem(40);
padding-top: rem(120);
text-align: center;
}
.p-rooms::before{
    z-index: 1;
    position: absolute;
    width: 100%;
    height:200px;
    height: 7.96vw;
    width: 100vw;
    top: -14%;
    left: 0;
    content: "";
    background: url(../../../images/common/wave-up.png)no-repeat center center / contain;
@include mq(md){
    top: -4%;
    }
}

.p-rooms::after{
    position: absolute;
    width: 100%;
    height:200px;
    bottom: -17%;
    left: 0;
    content: "";
    background: url(../../../images/common/wave-down.png)no-repeat center center / contain;
@include mq(md){
    bottom: -12%;
    
    }
}
.p-rooms__inner{
margin: 0 auto;
width: 100%;
max-width: rem(1120);
text-align: center;

@include mq(md){
   display: block;
    }
}
.p-rooms__title{
text-align: center;
margin-bottom: rem(80);
position: relative;
display: inline-block;
}
.p-rooms__title::after{
    position: absolute;
    width: rem(74);
    height:rem(35);
     bottom: rem(-20);
     left: 50%;
     transform: translateX(-50%);
    content: "お部屋";
    font-size: rem(18);
    color: $navy;
}
.p-rooms__title::before{
    
    position: absolute;
    content: "";
    background: url(../../../images/common/flower.png)no-repeat center center / contain; 
    overflow: hidden;
    width: 7.45vw;
    height:8.7vw;
    top: -14%;
    left: -56%;
    display: block;
    transform: rotate(-36deg);
    @include mq(xl){
        width: 6.45vw;
        width: rem(123);
    height:7.7vw;
    height: rem(147);
    top: -32%;
    left: -67%;
        }
    @include mq(md){
        width: 13.45vw;
        height: 15.7vw;
        top: -12%;
        left: -51%;
        }
}
.p-rooms__left{
width: 60%;
position: relative;
height: auto;
@include mq(md){
    width: 100%;
    max-width: rem(600);
    margin: 0 auto;
    }
}
.p-rooms__figure{

    position: relative;
    width: 100%;
    height: 0;
    padding-top: 59.0% ;
    @include mq(md){
        padding-top: 51.6%;
        }
}
.p-rooms__figure img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.p-rooms__right{
    position: absolute;
    width: 100%;
    height:100%;
    top: 50%;
    transform: translateY(-41%);
    left: 86%;
    content: "";
    width: 42.5%;
    width: 100%;
    z-index: 2;
    text-align: left;
    @include mq(md){
        background: $white;
        position: static;
        width: 100%;
        max-width: rem(600);
        // right: 0;
        // left: 0;
        // top: 100%;
        top: 100%;
        transform: translateY(0%);
        left: 0;
        
        
        
        }
}
.rooms-texts{
    background: $white;
    display: inline-block;
    padding: rem(40)rem(60)rem(40)rem(60);

}
.rooms__text-top{
   
   
}
.rooms__text-bottom{
    padding-top: rem(80);
}
.rooms__list{
    font-family: $base-font-family;
    font-size: rem(18);
    letter-spacing: 5px;
}
.rooms-span-top{
    padding-left: rem(10);
}
.rooms-span-middle{
    padding-left: rem(8);
}
.rooms-span-time{
  padding-left: rem(14);  
}

